import React, { Component } from 'react';
import './App.css';
import clickSound from "./assets/click.mp3";

//
// - Constants and Globals
//

const MacroStates = {
	INACTIVE: 0,
	STARTING: 1,
	PLAYING:  2,
	PAUSED:   3,
	FINISHED: 4
}

const PlayerStates = {
	INACTIVE: 0,
	WAITING: 1,
	EXECUTING: 2,
	PAUSED: 3,
	CONCLUDED: 4
}


const monthLength = [31,28,31,30,31,30,31,31,30,31,30,31];

const ImagePath = process.env.PUBLIC_URL + "/assets/images/";
const MacroPath = process.env.PUBLIC_URL + "/assets/macros/";


//
// - Simple Date
//

function parseSimpleDate(dateString) {
	var year  = parseInt(dateString.substr(0, 4));
	var month = parseInt(dateString.substr(5, 6));
	var day   = parseInt(dateString.substr(8, 9));

	return [day, month, year];
}

class SimpleDate {
	constructor(day, month, year) {
		this.Day   = day;
		this.Month = month;
		this.Year = year;
	}

	isLeapYear(year) {
		if (year % 4 === 0) return false;
		if (year % 100 !== 0) return true;
		if (year % 400 !== 0) return false;
		return true;
    }

	isLastDayOfMonth() {
		return this.Day === monthLength[this.Month - 1];
    }

	increment() {
		// Compute next day
		this.Day += 1;

		// Get Month length
		var dayCount = monthLength[this.Month - 1];
		if (this.Month === 2 && this.isLeapYear(this.Year)) dayCount += 1;

		// If Month length has been exceeded
		if (this.Day > dayCount) {
			this.Month += 1;

			this.Day = 1;

			// If Year length has been exceeded
			if (this.Month > 12) {
				this.Year += 1;

				this.Month = 1;
            }
		}
    }

	toString() {
		var strMonth = "" + this.Month;
		if (this.Month < 10) strMonth = "0" + strMonth;

		var strDay = "" + this.Day;
		if (this.Day < 10) strDay = "0" + strDay;

		return this.Year + "-" + strMonth + "-" + strDay;
	}

	displayText() {
		var strMonth = "" + this.Month;
		if (this.Month < 10) strMonth = "0" + strMonth;

		var strDay = "" + this.Day;
		if (this.Day < 10) strDay = "0" + strDay;

		return strDay + " / " + strMonth + " / "+ this.Year;
    }
}


//
// - AUX Functions
//

function degToRad(angle) {
	return angle * (Math.PI / 180);
}

async function LoadJson(path) {
	var res = null;

	res = await fetch(path).then(response => { return response.json(); } );

	return res;
}

function generateButtonPress(button, onTime, offTime, count) {
	return [
		{
			name: "Press " + button,
			macro: [
				{
					button  : button,
					onTime  : onTime,
					offTime : offTime,
					count   : 1
				}
			],
			count: count
		}
	];
}

function generateJoystickPush(side, onTime, offTime, strength, angle) {
	if(side !== "left" && side !== "right") return [];

	return [
		{
			name: "Push " + side + " analog stick",
			macro: [
				{
					button   : side + "_stick_dir",
					onTime   : onTime,
					offTime  : offTime,
					count    : 1,
					strength : strength,
					angle    : angle
				}
			],
			count: 1
		}
	];
}

function playClickSound() {
	var audio = new Audio(clickSound);
	audio.play();
}


//
// - React Components
//

function PlayerButton(props) {
	var divStyle = { background : "#FFF" };
	var iconStyle = {};

	if(props.selected) {
		divStyle  = { background : "#000" };
		iconStyle = { filter : "invert(1)" };
	}

	return (
		<div className = "player-button" id = {props.name} style = {divStyle}
			onClick = {e => props.clickHandler()} >
			<img className = "icon" src = {props.src} alt = {props.name + " Button Icon"}
				style = {iconStyle} />
		</div>
	);
}

function MacroButton(props) {
	var style = { background : "white" };
	if(props.selected) style = {background : "black"};

	return (
		<div className = "macro">
			<div className = "macro-button" id = {props.name} style = {style}
				onClick = {e => props.clickHandler(props.index)}>
				<img className = "icon" src = {props.src} alt = {"Icon " + props.name}/>
			</div>
			<label className = "macro-button-label">
				{props.name}
			</label>
		</div>
	);
}

function ProgressBar(props) {
	const width = { width : `${props.percentage}%` };

	return (
		<div className = "progress-bar">
			<div className = "filler" style = {width} />
		</div>
	);
}

function KeyLog(props) {
	return (
		<div key = {props.ind} className = "key-log">
			<b className = "key-log-cell"> {props.index} </b>
			<b className = "key-log-cell"> {props.pressedKey} </b>
			<b className = "key-log-cell"> {props.start} </b>
			<b className = "key-log-cell"> {props.duration} </b>
		</div>
	);
}


class CheckBox extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(event) {
		return this.props.onChange(this.props.paramKey, event.target.checked);
	}

	render() {
		return (
			<input type = "checkbox" checked = {this.props.checked} onChange = {this.handleChange} />
		);
	}
}

class IntegerInput extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(event) {
		if(event.target.value === "") {
			this.props.onChange(1);
			return;
		}

		let value = Math.min(event.currentTarget.value, this.props.max);

		this.props.onChange(parseInt(value));
	}

	render() {
		return (
			<input type = "number" className = "integer-input"
			readOnly = {this.props.readonly}
			value = {this.props.value} onChange = {this.handleChange}
			max = {this.props.max} min = {this.props.min}/>
		);
	}
}

class DateInput extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(event) {
		if (event.target.value === "") {
			this.props.onChange("2020-01-01");
			return;
		}

		let value = event.currentTarget.value;

		this.props.onChange(value);
	}

	render() {
		return (
			<input type="date" className="date-input"
				readOnly={this.props.readonly}
				value={this.props.value} onChange={this.handleChange}
				max={this.props.max} min={this.props.min} />
		);
	}
}

class DropDownList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(event) {
		if(event.target.value === "") {
			this.props.onChange(0);
			return;
		}

		this.props.onChange(parseInt(event.currentTarget.value));
	}

	renderOptions() {
		return this.props.options.map((string, index) => {
			return (
				<option key = {"option" + index.toString()} value={index}>{string}</option>
			);
		});
	}

	render() {
		let options = this.renderOptions();

		return (
			<select className = "drop-down-list" onChange = {this.handleChange}>
			  {options}
			</select>
		);
	}
}


class ParameterInput extends React.Component {
	constructor(props) {
		super(props);

		this.state = {active : false};
	}

	render() {
		var params = this.props.parameters;

		switch(this.props.macro) {
			default:
				return (
					<div className = "macro-parameters" id = "DefaultParams">
					</div>
				);

			case "Time Skip":
				return (
					<div className="macro-parameters" id="TimeSkipParams">
						<div className="parameters-entry">
							<label className="parameter-label">
								Start Date
							</label>
							<div className="parameter">
								<DateInput id="startDate" name="start-date"
									value={params.startDate}
									onChange={value => this.props.eventHandler("startDate", value)}
									min="2000-01-01" max="2059-12-31"
								/>
							</div>
						</div>
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Skip Count
							</label>
							<div className = "parameter">
								<IntegerInput id = "daysToAdvance" name = "days-to-advance"
									value = {params.daysToAdvance}
									onChange = {value => this.props.eventHandler("daysToAdvance", value)}
									min = "1" max = "1000"
								/>
							</div>
						</div>
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Date Format
							</label>
							<div className = "parameter">
								<DropDownList id = "dateFormat" name = "date-format"
									value    = {params.dateFormat}
									options  = {["World", "US", "Chinese"]}
									onChange = {value => this.props.eventHandler("dateFormat", value)}
								/>
							</div>
						</div>
					</div>
				);

			case "Wonder Box":
				return (
					<div className = "macro-parameters" id = "WonderBoxParams">
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Count
							</label>
							<div className = "parameter">
								<IntegerInput id = "WTradeCount" name = "wonder-trade-count"
									value = {params.count}
									onChange = {value => this.props.eventHandler("count", value)}
									min = "1" max = "30"
								/>
							</div>
						</div>
					</div>
				);

			case "Egg Hatcher":
				return (
					<div className = "macro-parameters" id = "EggHatcherParams">
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Egg Cycles
							</label>
							<div className = "parameter">
								<DropDownList id = "eggCycles" name = "egg-cycles"
									value    = {params.eggCycles.toString()}
									options  = {["5", "10", "15", "20", "25", "30", "35", "40"]}
									onChange = {value => this.props.eventHandler("eggCycles", value)}
								/>
							</div>
						</div>
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Hatching Ability
							</label>
							<div className = "parameter">
								<CheckBox id = "hasHatchAbility" name = "hatch-ability"
									checked  = {params.ability}
									paramKey = "ability"
									onChange = {this.props.eventHandler}
								/>
							</div>
						</div>
						<div className = "parameters-entry">
							<label className = "parameter-label">
								Eggs in Box
							</label>
							<div className = "parameter">
								<IntegerInput id = "eggsInBox" name = "eggs-in-box"
									value = {params.eggsInBox}
									onChange = {value => this.props.eventHandler("eggsInBox", value)}
									min = "1" max = "30"
								/>
							</div>
						</div>
					</div>
				);
		}
	}
}

class StatusTable extends React.Component {
	constructor(props) {
		super(props);

		this.state = { active: false };
	}

	render() {
		var status = this.props.status;

		switch (this.props.macro) {
			default:
				return (
					<div className="macro-status" id="DefaultStatus">
					</div>
				);

			case "Time Skip":
				var advanced = 0;
				if (status.daysAdvanced !== undefined) advanced = status.daysAdvanced;

				var current = "01 / 01 / 2020";
				if (status.currentDate !== undefined) current = status.currentDate.displayText();

				return (
					<div className="macro-status" id="TimeSkipStatus">
						<div className="status-entry">
							<label className="status-label">
								Skips Done
							</label>
							<div className="status">
								{advanced}
							</div>
						</div>
						<div className="status-entry">
							<label className="status-label">
								Current Date
							</label>
							<div className="status">
								{current}
							</div>
						</div>
					</div>
				);
			
			case "Wonder Box":
				var traded = 0;
				if (status.traded !== undefined) traded = status.traded;

				var row = 0;
				if (status.currentRow !== undefined) row = status.currentRow;

				var column = 0;
				if (status.currentColumn !== undefined) column = status.currentColumn;

				var slot = "Row: " + row.toString() + " | Col: " + column.toString();

				return (
					<div className="macro-status" id="WonderBoxStatus">
						<div className="status-entry">
							<label className="status-label">
								Traded
							</label>
							<div className="status">
								{traded}
							</div>
						</div>
						<div className="status-entry">
							<label className="status-label">
								Box Slot
							</label>
							<div className="status">
								{slot}
							</div>
						</div>
					</div>
				);
			
			case "Egg Hatcher":
				var hatched = 0;
				if (status.hatched !== undefined) hatched = status.hatched;

				return (
					<div className="macro-status" id="EggHatcherStatus">
						<div className="status-entry">
							<label className="status-label">
								Hatched
							</label>
							<div className="status">
								{hatched}
							</div>
						</div>
					</div>
				);
		}
	}
}

class InfoOverlay extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {
		var display_style = {display : "none"};
		if(this.props.show) display_style.display = "flex";

		var sections = [];

		var i = 0;
		for(; i < this.props.sections.length; i++) {
			var section = this.props.sections[i];

			sections[i] = (
				<div className = "infoSection" key = {"infoSect_" + i.toString()}>
					<h2 className = "infoSectionTitle"> {section.title} </h2>
					{section.text}
				</div>
			);
		}

		return (
			<div id = "infoOverlay" className = "info-overlay" style = {display_style}>
				<div id = "infoOverlayOut" onMouseDown  = {this.props.onMouseDown} onTouchStart = {this.props.onTouchStart}>
				</div>
				<div id = "infoTextBox" className = "text-box">
					<h1 className = "infoTextTitle"> {this.props.title} </h1>
						{sections}
				</div>
			</div>
		);
	}
}


//
// - Back End Elements
//

function coroutine(f, obj, args) {
	var o = f.apply(obj, args);

	o.next();

	return function(x) {
		return o.next(x);
	}
}

class JSONManeger {
	constructor() {
		this.segments = {
			FstSkip      : {filename: "FirstSkip.json",           object: ""},
			FstSkipUS    : {filename: "FirstSkipUS.json",         object: ""},
			FstSkipCHN   : {filename: "FirstSkipCHN.json",        object: ""},
			AdvDay       : {filename: "AdvanceDay.json",          object: ""},
			AdvDayUS     : {filename: "AdvanceDayUS.json",        object: ""},
			AdvDayCHN    : {filename: "AdvanceDayCHN.json",       object: ""},
			GetEggs      : {filename: "GetEggs.json",             object: ""},
			Recenter     : {filename: "Recenter.json",            object: ""},
			OpenBox      : { filename: "OpenBox.json", object: "" },
			SelectInBox  : { filename: "SelectInBox.json", object: "" },
			StartWonder  : {filename: "StartWonderTrade.json",    object: ""},
			EndWonder    : {filename: "ConcludeWonderTrade.json", object: ""},
			Hatching     : {filename: "Hatching.json",            object: ""}
		};

		var entries = Object.entries(this.segments);

		this.loadedCount = 0;
		this.segmentCount = entries.length;
		this.loadConcluded = false;

		let i = 0;
		for(; i < this.segmentCount; i++) {
			this.loadMacro(entries[i][0]);
		}
	}

	async loadMacro(key) {
		var segment = this.segments[key];

		segment.object = await LoadJson(MacroPath + segment.filename);

		this.loadedCount += 1;

		if(this.loadedCount === this.segmentCount) {
			this.loadConcluded = true;
			console.log("JSON Load Concluded");
		}
	}

	getMacro(key) {
		var segment = this.segments[key];

		if (segment === undefined) {
			console.log("No macro associated with key: " + key);
			return undefined;
		}

		var copy = JSON.parse(JSON.stringify(segment.object));
		return copy;
	}
}


//
// - Macro
//

class MacroController {
	constructor(jsonM, name, icon) {
		this.jsonManager = jsonM;

		this.name = name;
		this.icon = icon;

		this.parameters = {};
		this.paramHandlers = {};

		this.info = [];

		this.state = {
			state: MacroStates.INACTIVE,
			progress: 0
		};
	}


	changeParameter(key, value) {
		let handler = this.paramHandlers[key];

		return handler(value);
	}

	getMacro(key) {
		if (!this.jsonManager.loadConcluded) return null;

		return this.jsonManager.getMacro(key);
	}


	getMacroState() {
		return this.state.state;
    }

	getMacroProgress() {
		return this.state.progress;
	}

	getDuration() {
		return 0;
    }


	start() {
		this.state.state = MacroStates.STARTING;
    }

	reset() {
		this.state.state = MacroStates.INACTIVE;
    }


	// Update
	update() {
		console.log("This should have been overriden.");
    }


	// Render
	getRenderData() {
		return {
			name: this.name,
			icon: ImagePath + this.icon,
			state: this.state,
			parameters: this.parameters,
			info: this.info
		};
	}
}

class TimeSkipMacro extends MacroController {
	constructor(jsonM) {
		super(jsonM, "Time Skip", "timeskip_icon.png");

		// Init Parameters
		this.parameters.startDate     = "2020-01-01";
		this.parameters.daysToAdvance = 1;
		this.parameters.dateFormat	  = 0;

		this.currentFormat = "";

		// Method Binding
		this.onStartDateChange     = this.onStartDateChange.bind(this);
		this.onDaysToAdvanceChange = this.onDaysToAdvanceChange.bind(this);
		this.onDateFormatChange    = this.onDateFormatChange.bind(this);

		// Assign methods to parameters
		this.paramHandlers = {
			startDate: this.onStartDateChange,
			daysToAdvance: this.onDaysToAdvanceChange,
			dateFormat: this.onDateFormatChange
		};

		// Prepare help text
		var text1 = (
			<p>
				<b>1-</b> In the <b>Console Settings</b>, turn <b>Synchronize Time</b> off.
				<br />
				<br />
				<b>2-</b> The <b>Date</b> in <b>Console Settings</b> must be the first of any 31 day month.
				<br />
				<br />
				<b>3-</b> Set <b>Skip Count</b> to the number of days to advance.
				<br />
				<br />
				<b>4-</b> Set <b>Date Format</b> to match your console's format.
			</p>
		);

		// Assign help text
		this.info = [
			{
				title: "SetUp",
				text: text1
			},
			{
				title: "How it works",
				text: "The frame of the seed will advance with each time the date is advanced. This means that we can just change the day, with the only downside being that when returning to the first of the month no advancement will happen. The macro takes this into account and adds repetitions so that in the end the correct number of skips is achieved."
			},
			{
				title: "Recommendations",
				text: "Avoid doing many skips in the Wild Area as this is known to sometimes result in game crashes. Use this macro indoors."
			}
		];

		// Internal state
		this.state.currentDate  = new SimpleDate(1,1,2020);
		this.state.daysAdvanced = 0;
	}

	getDuration() {
		return 10160 + this.parameters.daysToAdvance * 3440;
    }


	reset() {
		var dateInfo = parseSimpleDate(this.parameters.startDate);

		this.state.state        = MacroStates.INACTIVE;
		this.state.currentDate  = new SimpleDate(dateInfo[0], dateInfo[1], dateInfo[2]);
		this.state.daysAdvanced = 0;
    }


	// Parameter Handlers
	onStartDateChange(newDate) {
		if (this.parameters.startDate !== newDate) {
			this.parameters.startDate = newDate;

			var dateInfo = parseSimpleDate(newDate);
			this.state.currentDate = new SimpleDate(dateInfo[0], dateInfo[1], dateInfo[2]);

			return true;
		}

		return false;
    }

	onDaysToAdvanceChange(days) {
		if (this.parameters.daysToAdvance !== days) {
			this.parameters.daysToAdvance = days;

			return true;
		}

		return false;
	}

	onDateFormatChange(format) {
		if (this.parameters.dateFormat !== format) {
			this.parameters.dateFormat = format;

			switch (format) {
				default:
				case 0: // WORLD
					this.currentFormat = "";
					break;

				case 1: // US
					this.currentFormat = "US";
					break;

				case 2: // Chinese
					this.currentFormat = "CHN";
					break;
			}

			return true;
		}

		return false;
	}


	// Build Step
	AdvanceDay(days) {
		var macro = this.getMacro("AdvDay" + this.currentFormat);

		macro[0].count = days;

		return macro;
	}

	InitMacro() {
		return this.getMacro("FstSkip" + this.currentFormat);
	}


	// Update
	update() {
		var response = { state: 0, macroStep: undefined };

		switch (this.state.state) {
			default:
				break;

			case MacroStates.STARTING: // Starting
				response.macroStep = this.InitMacro();

				this.state.state = MacroStates.PLAYING; // Go to Day Skips
				break;

			case MacroStates.PLAYING: // Advancing
				if (!this.state.currentDate.isLastDayOfMonth()) {
					this.state.daysAdvanced += 1;
				}
				else {
					this.state.currentDate.Day = 0;
                }

				this.state.currentDate.increment();

				// If goal has not been achieved
				if (this.parameters.daysToAdvance > this.state.daysAdvanced) {
					response.macroStep = this.AdvanceDay(1);
				}
				else {
					response.macroStep = generateButtonPress("home", 100, 1000, 2);

					this.state.state = MacroStates.FINISHED; // Go to Concluded
				}
				break;
		}

		this.state.progress = this.state.daysAdvanced / this.parameters.daysToAdvance;

		response.state = this.state.state;

		return response;
    }
}

class WonderBoxMacro extends MacroController {
	constructor(jsonM) {
		super(jsonM, "Wonder Box", "wonderbox_icon.png");

		this.parameters.count = 1;

		this.onCountChange = this.onCountChange.bind(this);

		this.paramHandlers = {
			count: this.onCountChange
		};


		var text1 = (
			<p>
				<b>1-</b> In-Game, open your PC and go to the box with the pokemon to be wonder traded.
				<br />
				<br />
				<b>2-</b> Make sure there are no empty spots between pokemon.
				The macro will start trading from the top left corner and go left to right, top to bottom.
				<br />
				<br />
				<b>3-</b> Exit all menus.
				<br />
				<br />
				<b>4-</b> Set the <b>Count</b> parameter to how many pokemon you want to trade away from that box.
				<br />
				<br />
				<b>5-</b> The macro will pause after starting the trade. Press play after you see the trade has completed.
			</p>
		);

		this.info = [
			{
				title: "How to",
				text: text1
			}
		];


		// Internal State
		this.state.phase = 0;

		this.state.traded        = 0;
		this.state.currentRow    = 0;
		this.state.currentColumn = 0;
	}


	reset() {
		this.state.state = MacroStates.INACTIVE;

		this.state.traded        = 0;
		this.state.currentRow    = 0;
		this.state.currentColumn = 0;
    }


	// Parameter Handlers
	onCountChange(count) {
		if (this.parameters.count !== count) {
			this.parameters.count = count;

			return true;
		}

		return false;
	}


	// Build Macro
	SelectInBox(column, row) {
		var segments = this.getMacro("SelectInBox");

		let dx = column;
		let dy = row;

		if (dx > 0) {
			segments[0].macro[0].count = dx;
		}
		else {
			segments[0].macro[1].count = -1 * dx;
		}

		if (dy > 0) {
			segments[0].macro[2].count = dy;
		}
		else {
			segments[0].macro[3].count = -1 * dy;
		}

		return segments;
	}

	ConcludeTrade() {
		var segments = this.getMacro("EndWonder");

		segments[0].macro[2].offTime = this.parameters.waitTime * 1000;

		return segments;
	}


	update() {
		var response = { state: 0, macroStep: undefined };

		if (this.state.state === MacroStates.PAUSED)
			this.state.state = MacroStates.PLAYING;

		switch (this.state.state) {
			default:
				break;

			case MacroStates.STARTING: // Start the trade
				response.macroStep = [];

				response.macroStep = response.macroStep.concat(this.getMacro("StartWonder"));
				response.macroStep = response.macroStep.concat(this.SelectInBox(this.state.currentColumn, this.state.currentRow));
				response.macroStep = response.macroStep.concat(generateButtonPress("a",120,240,20));

				this.state.state = MacroStates.PAUSED; // Go to Paused
				break;

			case MacroStates.PLAYING: // Complete the trade
				response.macroStep = [];

				this.state.currentColumn += 1;

				if (this.state.currentColumn >= 6) {
					this.state.currentColumn = 0;
					this.state.currentRow += 1;
				}

				this.state.traded += 1;

				response.macroStep = response.macroStep.concat(generateButtonPress("y", 120, 25000, 1));

				if (this.state.traded < this.parameters.count) {
					this.state.state = MacroStates.STARTING;
				}
				else {
					this.state.state = MacroStates.FINISHED;
                }
				break;
		}

		this.state.progress = this.state.traded / this.parameters.count;

		response.state = this.state.state;

		return response;
	}
}

class EggHatcherMacro extends MacroController {
	constructor(jsonM) {
		super(jsonM, "Egg Hatcher", "egghatcher_icon.png");


		this.parameters.eggCycles = 5;
		this.parameters.ability   = false;
		this.parameters.eggsInBox = 1;


		this.onEggCyclesChange  = this.onEggCyclesChange.bind(this);
		this.onHasAbilityChange = this.onHasAbilityChange.bind(this);
		this.onEggsInBoxChange  = this.onEggsInBoxChange.bind(this);


		this.paramHandlers = {
			eggCycles: this.onEggCyclesChange,
			ability: this.onHasAbilityChange,
			eggsInBox: this.onEggsInBoxChange
		};


		var text1 = (
			<p>
				<b>1-</b> Open your PC and navigate to the Box with the eggs. Make sure that the box was filled from up to down, left to right.
				This means the eggs should be placed column per column not row per row as is normal, if the box is full this doesn't matter.
				<br />
				<br />
				<b>2-</b> You must leave the <b>Pokémon</b> option selected when exiting the game's menu.
				<br />
				<br />
				<b>3-</b> When starting the macro, the <b>Player Character</b> must be near the <b>Nursery</b> at the Wild Area and not on the bike.
				<br />
				<br />
				<b>4-</b> Set the <b>Egg Cycles</b> parameter to how many cycles the egg takes to hatch, this can be checked on sites like Bulbapedia.
				<br />
				<br />
				<b>5-</b> Check the <b>Hatch Ability</b> box if the first pokemon in the party has one of the following abilities: Flame Body, Magma Armor or Steam Engine.
				<br />
				<br />
				<b>6-</b> Set the <b>Eggs in Box</b> parameter to how many eggs you have in the box to be hatched.
			</p>
		);

		this.info = [
			{
				title: "SetUp",
				text: text1
			}
		];


		// Internal State
		this.state.phase = 0;

		this.state.hatched      = 0;
		this.state.currentBatch = 0;
	}


	reset() {
		this.state.phase = 0;

		this.state.hatched      = 0;
		this.state.currentBatch = 0;
	}


	// Parameter Handlers
	onEggCyclesChange(cycles) {
		cycles = 5 * (parseInt(cycles) + 1);

		if (this.parameters.eggCycles !== cycles) {
			this.parameters.eggCycles = cycles;

			return true;
		}

		return false;
	}

	onHasAbilityChange(bool) {
		if (this.parameters.ability !== bool) {
			this.parameters.ability = bool;

			return true;
		}

		return false;
	}

	onEggsInBoxChange(count) {
		if (this.parameters.eggsInBox !== count) {
			this.parameters.eggsInBox = count;

			return true;
		}

		return false;
	}


	// Build Macro
	WithdrawBatch(eggCount) {
		let segments = this.getMacro("GetEggs");

		//Get the first egg
		let firstEgg = segments[0];

		firstEgg.macro[1].count = this.state.currentBatch;
		firstEgg.macro[3].count = this.state.currentBatch + 1;

		segments[0] = firstEgg;

		//Get the remaining eggs
		let remEggs = segments[0];

		remEggs.count = eggCount;

		if (eggCount > 0) {
			remEggs.macro[1].count = this.state.currentBatch + 1;
			remEggs.macro[3].count = this.state.currentBatch + 1;
		}

		segments[1] = remEggs;

		// Exit PC to Menu
		segments = segments.concat(generateButtonPress("b", 180, 1720, 2));

		return segments;
    }

	HatchSegment(eggsInParty) {
		let segments = this.getMacro("Hatching");

		// Calculate steps to hatch egg
		let cycles = this.parameters.eggCycles;
		if (this.parameters.ability) cycles = cycles / 2;

		// Calculate time to hatch
		let time = cycles * 6400;

		segments[0].macro[2].onTime = time;
		segments[0].macro[3].onTime = time;

		// Nudge forward to hatch consecutive eggs
		segments[1].count = eggsInParty;

		return segments;
	}


	update() {
		var response = { state: 0, macroStep: undefined };

		var hatching = 0;

		if (this.state.state === MacroStates.PAUSED)
			this.state.state = MacroStates.PLAYING;

		switch (this.state.state) {
			default:
				break;

			case MacroStates.STARTING: // Get a batch of eggs
				response.macroStep = [];

				response.macroStep = response.macroStep.concat(this.getMacro("OpenBox"));

				hatching = Math.min(this.parameters.eggsInBox - this.state.hatched, 5);

				response.macroStep = response.macroStep.concat(this.WithdrawBatch(hatching));

				response.macroStep = response.macroStep.concat(this.getMacro("Recenter"));

				this.state.state = MacroStates.PLAYING; // Go to Playing
				break;

			case MacroStates.PLAYING: // Hatch the eggs
				response.macroStep = [];

				switch (this.state.phase) {
					default:
						break;

					case 0: // Hatch
						// Hatch Eggs
						response.macroStep = response.macroStep.concat(this.HatchSegment(hatching));

						this.state.phase = 1;
						break;

					case 1: // Reset
						this.state.hatched += hatching;
						hatching = 0;

						this.state.currentBatch += 1;

						// Re-select the Boxes
						response.macroStep = response.macroStep.concat(generateButtonPress(    "x", 180,  840, 1));
						response.macroStep = response.macroStep.concat(generateButtonPress("right", 180,  240, 1));
						response.macroStep = response.macroStep.concat(generateButtonPress(   "up", 180,  240, 1));
						response.macroStep = response.macroStep.concat(generateButtonPress(    "b", 180, 1720, 1));
						response.macroStep = response.macroStep.concat(generateButtonPress( "plus", 180, 1200, 1));

						// Check for end
						if (this.state.hatched === this.parameters.eggsInBox) {
							this.state.state = MacroStates.FINISHED;
						}
						else {
							this.state.state = MacroStates.STARTING;
						}

						this.state.phase = 0;
						break;
                }
				break;
		}

		this.state.progress = this.state.traded / this.parameters.count;

		response.state = this.state.state;

		return response;
	}
}

