//
// - APP Components
//

class MacroExecuter {
	constructor() {
		this.state = MacroStates.INACTIVE;

		this.onWait = false;

		this.executing = null;

		this.commands = [];
	}


	execute(toExecute) {
		var f = function* (toExecute) {
			var info = { state: this.state, abort: false };

			// For each Segment
			var i = 0;
			for(; i < toExecute.length; i++) {
				var segment = toExecute[i];

				var steps = segment.macro;
				var reps  = segment.count;

				// For each Repetition of the Segment
				var j = 0;
				for(; j < reps; j++) {

					// For each Step of the Segment
					var k = 0;
					for(; k < steps.length; k++) {

						// If play has not been pressed or has been paused
						while (this.state !== PlayerStates.EXECUTING) {
							info = yield undefined; // Yield

							if(info.abort) return;

							this.state = info.state;
						}

						// Get current Step
						var step = steps[k];

						// Make command
						let command = this.makeCommand(step.button,  true, step.strength, step.angle);
						let reset   = this.makeCommand(step.button, false,             0,          0);

						// Check if input blocks progress
						let block = step.block === undefined || step.block === true;

						// For each repetition of the Step
						var l = 0;
						for(l = 0; l < step.count; l++) {
							this.commands.push(command);

							// Set timed command
							this.timedCommand(reset, block, Math.max(step.onTime, 20));

							yield this.commands;
							this.commands = [];

							// Wait
							while(this.onWait) {
								info = yield undefined;
								if(info.abort) return;
								this.state = info.state;
							}

							// If there is a buffer before the next button press
							if(block === true && step.offTime > 0) {
								this.onWait = true;

								// Set timer for the wait before pressing the next button
								this.timedCommand(null, block, Math.max(step.offTime, 20));
							}

							yield this.commands;
							this.commands = [];

							// Wait for timer to elapse and to not be paused
							while(this.onWait || this.state === PlayerStates.PAUSED) {
								info = yield undefined;
								if(info.abort) return;
								this.state = info.state;
							}

							this.currentOverallStep++;
						}
					}
				}
			}
			return;
		};

		// Create the Coroutine
		this.executing = coroutine(f, this, [toExecute]);
	}


	makeCommand(button, pressed, strength, angle) {
		return {
			button   : button,
			pressed  : pressed,
			strength : strength,
			angle    : angle
		};
	}

	timedCommand(command, blocking, duration) {
		setTimeout(this.issueCommand, duration, this, command, blocking); // New Timer

		// Block Macro progress
		this.onWait = blocking;
	}

	issueCommand(obj, command, blocking) {
		if(command !== null) {
			obj.commands.push(command);
		}

		// If step was blocking, unblock
		if(blocking) obj.onWait = false;
	}


	reset() {
		this.state = PlayerStates.INACTIVE;

		if(this.onWait) {
			this.onWait = false;

			clearTimeout(this.waitTimeout);

			this.waitTimeout = null;
		}

		if (this.executing !== null) {
			this.executing({state : this.state, abort : true});

			this.executing = null;
		}
	}
}

class MacroPlayer {
	constructor() {
		this.state = {
			selectedMacro: 0,
			playState: PlayerStates.INACTIVE,
			macroProgress: 0,
			macroDuration: 0,
			macroRunTime: 0,
			loadConcluded : false
		};

		// Create JSON Manager
		this.jsonManager = new JSONManeger();

		// Create Macro Controllers
		this.controllers = [];
		this.controllers[0] = new TimeSkipMacro(this.jsonManager);
		this.controllers[1] = new WonderBoxMacro(this.jsonManager);
		this.controllers[2] = new EggHatcherMacro(this.jsonManager);

		// Bind Input Methods
		this.selectMacro     = this.selectMacro.bind(this);
		this.changeParameter = this.changeParameter.bind(this);

		// Create Macro Executer
		this.executer = new MacroExecuter();
	}


	checkLoadConcluded() {
		this.state.loadConcluded = this.jsonManager.loadConcluded;
	}


	selectMacro(index) {
		if(this.state.selectedMacro === index) return false;

		playClickSound();

		var selected = this.controllers[this.state.selectedMacro];

		var macroState = selected.getMacroState();

		if(macroState !== MacroStates.INACTIVE) {
			selected.reset();

			this.state.playState     = PlayerStates.INACTIVE;
			this.state.macroProgress = 0;

			this.executer.reset();
		}

		this.state.selectedMacro = index;

		return true;
	}

	changeParameter(key, value) {
		let controller = this.controllers[this.state.selectedMacro];

		controller.changeParameter(key, value);
	}


	play() {
		switch (this.state.playState) {
			default:
			case PlayerStates.WAITING:
			case PlayerStates.EXECUTING:
				return false;

			case PlayerStates.PAUSED:
				if (this.executer.executing !== undefined) {
					this.state.playState = PlayerStates.EXECUTING;
				}
				else {
					this.state.playState = PlayerStates.WAITING;
                }
				return true;

			case PlayerStates.INACTIVE:
			case PlayerStates.CONCLUDED:
				this.reset();

				var selected = this.controllers[this.state.selectedMacro];

				selected.start();

				this.state.macroDuration = selected.getDuration();
				this.state.macroRunTime  = 0;

				this.state.playState = PlayerStates.WAITING;

				return true;
		}
	}

	pause() {
		if (this.state.playState === PlayerStates.WAITING ||
			this.state.playState === PlayerStates.EXECUTING) {
			this.state.playState = PlayerStates.PAUSED;

			return true;
		}

		return false;
	}

	reset() {
		var selected = this.controllers[this.state.selectedMacro];

		if (this.state.playState !== PlayerStates.INACTIVE) {
			selected.reset();

			this.state.playState = PlayerStates.INACTIVE;
			this.state.macroProgress = 0;
			this.state.macroDuration = 0;
			this.state.macroRunTime  = 0;

			return true;
		}

		return false;
	}


	getMacroState() {
		return {
			macroState: this.state.playState,
			macroProgress: this.state.macroProgress,
			macroDuration: this.state.macroDuration,
			macroRunTime: this.state.macroRunTime
		};
	}

	getAllMacroData() {
		let dataArray = [];

		let i = 0;
		for(; i < this.controllers.length; i++) {
			let controller = this.controllers[i];

			dataArray[i] = controller.getRenderData();
		}

		return dataArray;
	}

	getMacroRenderData() {
		var controller = this.controllers[this.state.selectedMacro];

		return controller.getRenderData();
	}


	update(currentTime) {
		var response  = undefined;
		var runResult = undefined;

		if(!this.state.loadConcluded) {
			this.checkLoadConcluded();
		}

		// If Load has been completed
		if(this.state.loadConcluded) {
			// Get selected Macro's Controller
			var selected = this.controllers[this.state.selectedMacro];

			// If waiting for the Macro controller to issue the next step
			if (this.state.playState === PlayerStates.WAITING) {
				var result = selected.update(currentTime);

				this.state.macroProgress = selected.getMacroProgress() * 100;

				// If there was a result from updating the Controller
				if (result !== undefined && result !== null) {
					var macro = result.macroStep;

					// If a new macro step was issued
					if (macro !== undefined) {
						this.executer.execute(macro); // Start execution

						this.state.playState = PlayerStates.EXECUTING; // Go to Executing
					}
                }
            }

			// Give Executer Orders and get result
			switch (this.state.playState) {
				default:
					return;

				case PlayerStates.EXECUTING:
					this.state.macroRunTime += 20;

					runResult = this.executer
						.executing({ state: this.state.playState, abort: false });
					break;

				case PlayerStates.PAUSED:
					if (this.executer.executing !== undefined) {
						runResult = this.executer
							.executing({ state: this.state.playState, abort: false });
					}
					break;
			}

			// If a result was obtained
			if(runResult !== undefined) {
				// If the result includes a value
				if(runResult.value !== undefined) {
					response = runResult.value;
				}

				// If this Step execution has concluded
				if (runResult.done) {
					var macroState = selected.getMacroState();

					switch (macroState) {
						default:
							this.state.playState = PlayerStates.WAITING;
							break;

						case MacroStates.PAUSED:
							this.state.playState = PlayerStates.PAUSED;
							break;

						case MacroStates.FINISHED:
							this.state.playState = PlayerStates.CONCLUDED;
							break;
                    }

					this.executer.executing = undefined;
				}
			}
		}

		return response;
	}
}

class KeyLogger {
	constructor() {
		this.keys = {
			a               : {pressed : false, pendingLog: null},
			b               : {pressed : false, pendingLog: null},
			x               : {pressed : false, pendingLog: null},
			y               : {pressed : false, pendingLog: null},
			l               : {pressed : false, pendingLog: null},
			lsl             : {pressed : false, pendingLog: null},
			lsr             : {pressed : false, pendingLog: null},
			r               : {pressed : false, pendingLog: null},
			rsl             : {pressed : false, pendingLog: null},
			rsr             : {pressed : false, pendingLog: null},
			plus            : {pressed : false, pendingLog: null},
			minus           : {pressed : false, pendingLog: null},
			up              : {pressed : false, pendingLog: null},
			right           : {pressed : false, pendingLog: null},
			left            : {pressed : false, pendingLog: null},
			down            : {pressed : false, pendingLog: null},
			left_stick      : {pressed : false, pendingLog: null},
			right_stick     : {pressed : false, pendingLog: null},
			left_stick_dir  : {pressed : false, pendingLog: null},
			right_stick_dir : {pressed : false, pendingLog: null},
			home            : {pressed : false, pendingLog: null}
		};

		this.startTime = 0;

		this.log = [];
		this.log_count = 0;
	}

	clear() {
		this.keys = {
			a               : {pressed : false, pendingLog: null},
			b               : {pressed : false, pendingLog: null},
			x               : {pressed : false, pendingLog: null},
			y               : {pressed : false, pendingLog: null},
			l               : {pressed : false, pendingLog: null},
			lsl             : {pressed : false, pendingLog: null},
			lsr             : {pressed : false, pendingLog: null},
			r               : {pressed : false, pendingLog: null},
			rsl             : {pressed : false, pendingLog: null},
			rsr             : {pressed : false, pendingLog: null},
			plus            : {pressed : false, pendingLog: null},
			minus           : {pressed : false, pendingLog: null},
			up              : {pressed : false, pendingLog: null},
			right           : {pressed : false, pendingLog: null},
			left            : {pressed : false, pendingLog: null},
			down            : {pressed : false, pendingLog: null},
			left_stick      : {pressed : false, pendingLog: null},
			right_stick     : {pressed : false, pendingLog: null},
			left_stick_dir  : {pressed : false, pendingLog: null},
			right_stick_dir : {pressed : false, pendingLog: null},
			home            : {pressed : false, pendingLog: null}
		};

		this.startTime = 0;

		this.log = [];
		this.log_count = 0;
	}

	pressKey(key, time) {
		if(key !== "buffer") {
			let info = this.keys[key];

			// If key is not pressed
			if(info.pressed === false) {
				info.pressed = true;

				if(this.startTime === 0) {
					this.startTime = time;
				}

				info.pendingLog = this.addLogEntry(key, time - this.startTime);
			}

			this.keys[key] = info;
		}
	}

	addLogEntry(key, time) {
		let entry = {index: this.log_count, key: key, start: time, duration: 0}

		this.log[this.log_count] = entry;

		this.log_count++;

		return entry;
	}

	releaseKey(key, time) {
		if(key !== "buffer") {
			let info = this.keys[key];

			if(info.pressed === true) {
				info.pressed = false;

				let entry = info.pendingLog;
				info.pendingLog = null;

				entry.duration = time - entry.start - this.startTime;

				this.log[entry.index] = entry;
			}
		}
	}

	getPressed() {
		let pressedKeys = [];

		for(let entry in this.keys) {
			let info = this.keys[entry];

			if(info.pressed === true) {
				pressedKeys.push(entry);
			}
		}

		return pressedKeys;
	}

	update(key, pressed) {
		let time = (new Date()).getTime();

		// If key is being pressed
		if(pressed) {
			this.pressKey(key, time);
		}
		// If key is being released
		else {
			this.releaseKey(key, time);
		}
	}

	renderPressed() {
		let info = Object.entries(this.keys);

		let render = [];
		let count  =  0;

		let i = 0;
		for(; i < info.length; i++) {
			let keyData = info[i];

			if(keyData[1].pressed === true) {
				render[count] = (
					<div className = "pressedKey" key = {keyData[0]}>
						{keyData[0]}
					</div>
				);

				count++;
			}
		}

		return (
			<div id = "PressedKeys">
				<b> Pressed </b>
				{render}
			</div>
		);
	}

	renderLogged() {
		var keyLogs = [];

		if(this.log_count > 0) {
			var i = 0;
			for(; i < Math.min(3, this.log_count); i++) {
				var l = this.log[this.log_count - (i + 1)];
				keyLogs.push(
					<KeyLog key = {"keylog_" + i} index = {l.index.toString()}
						pressedKey = {l.key} start = {l.start} duration = {l.duration}/>
				);
			}
		}

		return (
			<div id = "KeyLogs">
				{keyLogs}
			</div>
		);
	}
}


//
// - APP
//

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {};

		this.onButtonEvent   = this.onButtonEvent.bind(this);
		this.selectMacro     = this.selectMacro.bind(this);
		this.parameterChange = this.parameterChange.bind(this);

		this.macroPlayer = new MacroPlayer();

		this.keyLogger = new KeyLogger();
		this.currentTime = 0;

		this.state = {displayStartM : true,
					  selectedMacro :  0,
					  macroState    : -1,
					  macroProgress :  0,
					  macroDuration :  0,
					  macroRunTime  :  0,
					  displayInfo   : false
		};
	}


	componentDidMount() {
		this.UpdateTimerId = setInterval(
			() => this.update(),
			20
		);
	}

	componentWillUnmount() {
		clearInterval(this.UpdateTimerId);
	}


	update() {
		this.currentTime += 20;

		var res = this.macroPlayer.update(this.currentTime);

		this.setState(this.macroPlayer.getMacroState());

		// If there was an Update
		if(res !== undefined) {
			// Iterate the Log
			let i;
			for(i = 0; i < res.length; i++) {
				let command = res[i];

				console.log(command);

				// If it was a JoyStick Update
				if(command.button === "right_stick_dir" ||
					 command.button === "left_stick_dir") {
					this.handleSwitchJoySticks(command.button, command.strength, command.angle);
				}

				// If it was a Key Update
				else {
					this.handleSwitchKeys(command.button, command.pressed);
				}

				this.keyLogger.update(command.button, command.pressed);
			}
		}
	}


	onButtonEvent(name) {
		var audio = new Audio(process.env.PUBLIC_URL + "/assets/click.mp3");
		audio.play();

		switch (name) {
			default:
				return;

			case "startMessage":
				let pressedSM = !this.state.displayStartM;
				this.setState({ displayStartM: pressedSM });
				break;

			case "play":
				this.macroPlayer.play();
				break;

			case "pause":
				this.macroPlayer.pause();
				break;

			case "reset":
				if(this.macroPlayer.reset()) {
					var pressedKeys = this.keyLogger.getPressed();

					let i;
					for(i = 0; i < pressedKeys.length; i++) {
						let key = pressedKeys[i];

						if(key === "left_stick_dir" ||
							 key === "right_stick_dir") {
							this.handleSwitchJoySticks(key, 0, 0);
						}
						else {
							this.handleSwitchKeys(key, false);
						}
					}

					this.keyLogger.clear();
				}
				break;

			case "info":
				let pressedI = !this.state.displayInfo;
				this.setState({displayInfo: pressedI});
				break;
		}

		this.setState(this.macroPlayer.getMacroState());
	}


	selectMacro(index) {
		if(this.macroPlayer.selectMacro(index)) {
			this.keyLogger.clear();
		}
	}

	parameterChange(key, value) {
		this.macroPlayer.changeParameter(key, value);
	}


	handleSwitchJoySticks(joystick, strength, angle) {
		let rads = degToRad(angle);

		if(window.joyconJS === undefined) return;

		if(joystick === "right_stick_dir") {
			window.joyconJS.onRightJoystick(strength, rads);
		}

		if(joystick === "left_stick_dir") {
			window.joyconJS.onLeftJoystick(strength, rads);
		}
	}

	handleSwitchKeys(name, pressed) {
		if(window.joyconJS === undefined) return;

		if(name === "minus") {
			window.joyconJS.onMinus(pressed);
		}

		if(name === "left-stick") {
			window.joyconJS.onLeftJoystickPressed(pressed);
		}

		if(name === "up") {
			window.joyconJS.onUp(pressed);
		}

		if(name === "right") {
			window.joyconJS.onRight(pressed);
		}

		if(name === "down") {
			window.joyconJS.onDown(pressed);
		}

		if(name === "left") {
			window.joyconJS.onLeft(pressed);
		}

		if(name === "l") {
			window.joyconJS.onL(pressed);
		}

		if(name === "lsl") {
			window.joyconJS.onLeftSL(pressed);
		}

		if(name === "lsr") {
			window.joyconJS.onLeftSR(pressed);
		}

		if(name === "plus") {
			window.joyconJS.onPlus(pressed);
		}

		if(name === "a") {
			window.joyconJS.onA(pressed);
		}

		if(name === "b") {
			window.joyconJS.onB(pressed);
		}

		if(name === "x") {
			window.joyconJS.onX(pressed);
		}

		if(name === "y") {
			window.joyconJS.onY(pressed);
		}

		if(name === "r") {
			window.joyconJS.onR(pressed);
		}

		if(name === "rsl") {
			window.joyconJS.onRightSL(pressed);
		}

		if(name === "rsr") {
			window.joyconJS.onRightSR(pressed);
		}

		if(name === "right-stick") {
			window.joyconJS.onRightJoystickPressed(pressed);
		}

		if(name === "home") {
			window.joyconJS.onHome(pressed);
		}
	}


	renderStartMessage() {
		const text = (
			<b> "lorem ipsum" </b>
		);

		return (
			<div id="Start Message">
				<InfoOverlay show={this.state.displayStartM}
					title="Welcome" sections={text}
					onMouseDown={e => this.onButtonEvent("startMessage")}
					onTouchStart={e => this.onButtonEvent("startMessage")} />
			</div>
		);
	}

	renderMacros() {
		if(!this.macroPlayer.state.loadConcluded) return (<b> Loading </b>);

		let selected = this.macroPlayer.state.selectedMacro;

		let data = this.macroPlayer.getAllMacroData();

		let index = -1;

		return data.map((entry) => {
				index++;

				var isSelected = selected === index;

				return (
					<MacroButton key = {"macro_" + index.toString()} index = {index}
						selected = {isSelected} name = {entry.name} src = {entry.icon}
						clickHandler = {this.selectMacro}/>
				);
			}
		);
	}

	renderParameters() {
		if(!this.macroPlayer.state.loadConcluded) return (<b> Loading </b>);

		let data = this.macroPlayer.getMacroRenderData();

		return (
			<ParameterInput key = {"macroParams"}
				macro = {data.name} parameters = {data.parameters}
				eventHandler = {this.parameterChange}/>
		);
	}

	renderStatus() {
		if (!this.macroPlayer.state.loadConcluded) return (<b> Loading </b>);

		let data = this.macroPlayer.getMacroRenderData();

		return (
			<StatusTable key={"macroStatus"}
				macro={data.name} status={data.state} />
		);
    }

	renderInfo() {
		let data = this.macroPlayer.getMacroRenderData();
		let name = data.name;
		let info = data.info;

		return (
			<div id = "Info">
				<button className = "info-button" id = "InfoButton"
					onMouseUp    = {e => this.onButtonEvent("info")}
					onTouchStart = {e => this.onButtonEvent("info")}>
					Info
				</button>
				<InfoOverlay show = {this.state.displayInfo}
					title = {name} sections = {info}
					onMouseUp    = {e => this.onButtonEvent("info")}
					onTouchStart = {e => this.onButtonEvent("info")}/>
			</div>
		);
	}

	renderTime() {
		var runMins = Math.floor(this.state.macroRunTime / 60000);
		if (runMins < 10) runMins = "0" + runMins;

		var runSecs = Math.floor((this.state.macroRunTime % 60000) / 1000);
		if (runSecs < 10) runSecs = "0" + runSecs;

		var runMili = ((this.state.macroRunTime % 60000) % 1000);
		if (runMili < 10) runMili = "000" + runMili;
		else if (runMili < 100) runMili = "00" + runMili;
		else if (runMili < 1000) runMili = "0" + runMili;

		var runTime = runMins + ":" + runSecs + ":" + runMili;

		
		var durMins = Math.floor(this.state.macroDuration / 60000);
		if (durMins < 10) durMins = "0" + durMins;

		var durSecs = Math.floor((this.state.macroDuration % 60000) / 1000);
		if (durSecs < 10) durSecs = "0" + durSecs;

		var durMili = ((this.state.macroDuration % 60000) % 1000);
		if (durMili < 10) durMili = "000" + durMili;
		else if (durMili < 100) durMili = "00" + durMili;
		else if (durMili < 1000) durMili = "0" + durMili;

		var durTime = durMins + ":" + durSecs + ":" + durMili;

		var time = runTime + " / " + durTime;
		

		return (
			<div id="MacroTime">
				{time}
			</div>
		);
    }


	render() {
		const startM = this.renderStartMessage();

		const macros     = this.renderMacros();
		const parameters = this.renderParameters();
		const status     = this.renderStatus();
		const info       = this.renderInfo();
		const time       = this.renderTime();

		const current = this.macroPlayer.state.playState;

		const appIcon = ImagePath + "macro_app_icon.png";

		const resetIcon = ImagePath + "reset_icon.png";
		const playIcon  = ImagePath + "play_icon.png";
		const pauseIcon = ImagePath + "pause_icon.png";

		return (
			<div className="App">
				<div className="App-header">
					<img className="app-icon" alt="AppIcon" src={appIcon} />
					<b className = "App-title"
						style = {{color: "black"}}> POKÉMACROS </b>
				</div>
				<div id="body">
					{startM}
					<div id = "Macros">
						{macros}
					</div>
					<div id = "MacroInfo">
						{parameters}
						<br/>
						{status}
						<br/>
						{info}
					</div>
					<div id="Player">
						<ProgressBar key="progressbar" percentage={this.state.macroProgress} />
						{time}
						<div id="PlayerButtons">
							<PlayerButton id = "ResetButton" selected = {current === MacroStates.INACTIVE} name = "Reset"
								src = {resetIcon} clickHandler = {e => this.onButtonEvent("reset")} />

							<PlayerButton id = "PlayButton"  selected = {current === MacroStates.PLAYING}  name = "Play"
								src = {playIcon}  clickHandler = {e => this.onButtonEvent("play")} />

							<PlayerButton id = "PauseButton" selected = {current === MacroStates.PAUSED}   name = "Pause"
									src={pauseIcon} clickHandler={e => this.onButtonEvent("pause")} />
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default App;
